@import url(../assets/css/swiper-3.3.1.min.css);

.swiper-container {
  height: 6.133rem;
}

.swiper-slide {
  -webkit-background-size: 100% 100%;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.swiper-slide .swiper-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.1) 40%, rgba(0, 0, 0, 0.1) 100%);
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.7)), color-stop(40%, rgba(0, 0, 0, 0.1)), to(rgba(0, 0, 0, 0.1)));
  background-image: linear-gradient(bottom, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.1) 40%, rgba(0, 0, 0, 0.1) 100%);
}

.swiper-slide .slider-title {
  position: absolute;
  bottom: 0.613rem;
  line-height: 1.2;
  left: 0;
  padding: 0 0.48rem;
  font-weight: 300;
  font-size: 0.56rem;
  color: #ffffff;
}

.swiper-pagination-bullet {
  width: 0.16rem;
  height: 0.16rem;
  display: inline-block;
  border-radius: 100%;
  background: #5a5a5a;
  opacity: .8;
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: #f4f5ff;
}


