@charset "UTF-8";

.detail-content {
  /*.headline {*/
  /*border-bottom: 4px solid #f6f6f6;*/
  /*}*/
  /* ==== */
  /*禁用头部下面的分隔线*/
}

.detail-content article,
.detail-content aside,
.detail-content details,
.detail-content figcaption,
.detail-content figure,
.detail-content footer,
.detail-content header,
.detail-content hgroup,
.detail-content main,
.detail-content nav,
.detail-content section,
.detail-content summary {
  display: block;
}

.detail-content audio,
.detail-content canvas,
.detail-content video {
  display: inline-block;
}

.detail-content audio:not([controls]) {
  display: none;
  height: 0;
}

.detail-content html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
}

.detail-content body {
  font-family: 'Helvetica Neue', Helvetica, Arial, Sans-serif;
  background: #fff;
  padding-top: 0;
  margin: 0;
}

.detail-content a:focus {
  outline: thin dotted;
}

.detail-content a:active,
.detail-content a:hover {
  outline: 0;
}

.detail-content h1 {
  margin: .67em 0;
}

.detail-content h1,
.detail-content h2,
.detail-content h3,
.detail-content h4,
.detail-content h5,
.detail-content h6 {
  font-size: 0.427rem;
}

.detail-content abbr[title] {
  border-bottom: 0.027rem dotted;
}

.detail-content hr {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}

.detail-content mark {
  background: #ff0;
  color: #000;
}

.detail-content code,
.detail-content kbd,
.detail-content pre,
.detail-content samp {
  font-family: monospace,serif;
  font-size: 1em;
}

.detail-content pre {
  white-space: pre-wrap;
}

.detail-content q {
  quotes: \201C\201D\2018\2019;
}

.detail-content small {
  font-size: 80%;
}

.detail-content sub,
.detail-content sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

.detail-content sup {
  top: -0.5em;
}

.detail-content sub {
  bottom: -0.25em;
}

.detail-content img {
  border: 0;
  vertical-align: middle;
  color: transparent;
  font-size: 0;
}

.detail-content svg:not(:root) {
  overflow: hidden;
}

.detail-content figure {
  margin: 0;
}

.detail-content fieldset {
  border: 0.027rem solid silver;
  margin: 0 0.053rem;
  padding: .35em .625em .75em;
}

.detail-content legend {
  border: 0;
  padding: 0;
}

.detail-content table {
  border-collapse: collapse;
  border-spacing: 0;
  overflow: hidden;
}

.detail-content a {
  text-decoration: none;
}

.detail-content blockquote {
  border-left: 0.08rem solid #D0E5F2;
  font-style: normal;
  display: block;
  vertical-align: baseline;
  font-size: 100%;
  margin: .5em 0;
  padding: 0 0 0 1em;
}

.detail-content ul,
.detail-content ol {
  padding-left: 0.533rem;
}

.detail-content .main-wrap {
  max-width: 100%;
  min-width: 8rem;
  margin: 0 auto;
}

.detail-content .content-wrap {
  overflow: hidden;
  background-color: #f9f9f9;
}

.detail-content .content-wrap a {
  word-break: break-all;
}

.detail-content .headline-title.onlyheading {
  margin: 0.533rem 0;
}

.detail-content .headline img {
  max-width: 100%;
  vertical-align: top;
}

.detail-content .headline-background-link {
  line-height: 2em;
  position: relative;
  display: block;
  padding: 0.533rem 1.2rem 0.533rem 0.533rem !important;
}

.detail-content .icon-arrow-right {
  position: absolute;
  top: 50%;
  right: 0.533rem;
  background-image: url(http://static.daily.zhihu.com/img/share-icons.png);
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
  background-position: -1.867rem -0.533rem;
  width: 0.267rem;
  height: 0.4rem;
  margin-top: -0.2rem;
}

.detail-content .headline-background .heading {
  color: #999;
  font-size: 0.4rem !important;
  margin-bottom: 0.213rem;
  line-height: 1em;
}

.detail-content .headline-background .heading-content {
  color: #444;
  font-size: 0.453rem !important;
  line-height: 1.2em;
}

.detail-content .headline-title {
  line-height: 1.2em;
  color: #000;
  font-size: 0.587rem;
  margin: 0.533rem 0 0.267rem;
  padding: 0 0.533rem !important;
  font-weight: bold;
}

.detail-content .meta {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.427rem;
  color: #b8b8b8;
}

.detail-content .meta .source-icon {
  width: 0.533rem;
  height: 0.533rem;
  margin-right: 0.107rem;
}

.detail-content .meta .time {
  float: right;
  margin-top: 0.053rem;
}

.detail-content .content {
  color: #444;
  line-height: 1.6em;
  font-size: 0.453rem;
  margin: 0.267rem 0 0.533rem;
}

.detail-content .content img {
  max-width: 100%;
  display: block;
  margin: 0.8rem auto;
}

.detail-content .content img + img {
  margin-top: 0.4rem;
}

.detail-content .content img[src*="zhihu.com/equation"] {
  display: inline-block;
  margin: 0 0.08rem;
}

.detail-content .content a {
  color: #259;
}

.detail-content .content a:hover {
  text-decoration: underline;
}

.detail-content .view-more {
  margin-bottom: 0.667rem;
  text-align: center;
}

.detail-content .view-more a {
  font-size: 0.427rem;
  display: inline-block;
  width: 3.333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: #f0f0f0;
  color: #B8B8B8;
}

.detail-content .content .view-more a {
  font-size: 0.427rem;
  display: inline-block;
  width: 3.333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: #f0f0f0;
  color: #B8B8B8;
}

.detail-content .question {
  overflow: hidden;
  padding: 0 0.533rem !important;
}

.detail-content .question + .question {
  border-top: 0.133rem solid #f6f6f6;
}

.detail-content .question-title {
  line-height: 1.4em;
  color: #000;
  font-weight: 700;
  font-size: 0.48rem;
  margin: 0.533rem 0;
}

.detail-content .meta .author {
  color: #444;
  font-weight: 700;
}

.detail-content .answer + .answer {
  border-top: 0.053rem solid #f6f6f6;
  padding-top: 0.533rem;
}

.detail-content .footer {
  text-align: center;
  color: #b8b8b8;
  font-size: 0.347rem;
  padding: 0.533rem 0;
}

.detail-content .footer a {
  color: #b8b8b8;
}

.detail-content .question .view-more a {
  width: 100%;
  display: block;
}

.detail-content .hot-comment {
  -webkit-tap-highlight-color: transparent;
}

.detail-content .comment-label {
  font-size: 0.427rem;
  color: #333;
  line-height: 1.5em;
  font-weight: 700;
  border-top: 0.027rem solid #eee;
  border-bottom: 0.027rem solid #eee;
  margin: 0;
  padding: 0.24rem 0.533rem;
}

.detail-content .comment-list {
  margin-bottom: 0.533rem;
}

.detail-content .comment-item {
  font-size: 0.4rem;
  color: #666;
  border-bottom: 0.027rem solid #eee;
  padding: 0.4rem 0.533rem;
}

.detail-content .comment-meta {
  position: relative;
  margin-bottom: 0.267rem;
}

.detail-content .comment-meta .author {
  vertical-align: middle;
  color: #444;
}

.detail-content .comment-meta .vote {
  position: absolute;
  color: #b8b8b8;
  font-size: 0.32rem;
  right: 0;
}

.detail-content .night .comment-label {
  color: #b8b8b8;
  border-top: 0.027rem solid #303030;
  border-bottom: 0.027rem solid #303030;
}

.detail-content .night .comment-item {
  color: #7f7f7f;
  border-bottom: 0.027rem solid #303030;
}

.detail-content .icon-vote,
.detail-content .icon-voted {
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: 0;
  width: 0.293rem;
  height: 0.32rem;
  margin-right: 0.107rem;
  background-image: url(http://static.daily.zhihu.com/img/app/Comment_Vote.png) !important;
}

.detail-content .icon-voted {
  background-image: url(http://static.daily.zhihu.com/img/app/Comment_Voted.png) !important;
}

.detail-content .night .icon-vote {
  background-image: url(http://static.daily.zhihu.com/img/app/Dark_Comment_Vote.png) !important;
}

.detail-content .img-wrap .headline-title {
  bottom: 0.133rem;
}

.detail-content .img-wrap .img-source {
  right: 0.267rem !important;
  font-size: 0.24rem;
}

.detail-content .global-header {
  position: static;
}

.detail-content .button {
  width: 1.6rem;
}

.detail-content .button i {
  margin-right: 0;
}

.detail-content .headline .img-place-holder {
  height: 0;
}

.detail-content .from-column {
  width: 7.467rem;
  line-height: 0.8rem;
  height: 0.8rem;
  padding-left: 2.4rem;
  color: #2aacec;
  background-image: url(http://static.daily.zhihu.com/img/News_Column_Entrance.png);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0 0.533rem 0.533rem;
}

.detail-content .from-column:active {
  background-image: url(http://static.daily.zhihu.com/img/News_Column_Entrance_Highlight.png);
}

.detail-content .night .headline {
  border-bottom: 0.053rem solid #303030;
}

.detail-content .night img {
  -webkit-mask-image: -webkit-gradient(linear, 0 0, 0 100%, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7)));
}

.detail-content body.night,
.detail-content .night .content-wrap {
  background: #343434;
}

.detail-content .night .answer + .answer {
  border-top: 0.053rem solid #303030;
}

.detail-content .night .question + .question {
  border-top: 0.107rem solid #303030;
}

.detail-content .night .view-more a {
  background: #292929;
  color: #666;
}

.detail-content .night .icon-arrow-right {
  background-image: url(http://static.daily.zhihu.com/img/share-icons.png);
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
  background-position: -1.867rem -0.933rem;
  width: 0.267rem;
  height: 0.4rem;
}

.detail-content .night blockquote,
.detail-content .night sup {
  border-left: 0.08rem solid #666;
}

.detail-content .night .content a {
  color: #698ebf;
}

.detail-content .night .from-column {
  color: #2b82ac;
  background-image: url(http://static.daily.zhihu.com/img/Dark_News_Column_Entrance.png);
}

.detail-content .night .from-column:active {
  background-image: url(http://static.daily.zhihu.com/img/Dark_News_Column_Entrance_Highlight.png);
}

.detail-content .large .question-title {
  font-size: 0.64rem;
}

.detail-content .large .meta {
  font-size: 0.48rem;
}

.detail-content .large .content {
  font-size: 0.533rem;
}

.detail-content .large blockquote,
.detail-content .large sup {
  line-height: 1.6;
}

.detail-content .meta .meta-item {
  -o-text-overflow: ellipsis;
  width: 39%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  color: #929292;
  margin-right: 0.187rem;
}

.detail-content .headline .meta {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.293rem;
  color: #b8b8b8;
  margin: 0.4rem 0;
  padding: 0 0.533rem;
}

.detail-content .headline .meta a,
.detail-content .headline .meta a:hover {
  padding-left: 1em;
  margin-top: 0.053rem;
  float: right;
  font-size: 0.293rem;
  color: #0066cf;
  text-decoration: none;
}

.detail-content .highlight {
  width: auto;
  overflow: auto;
  word-wrap: normal;
}

.detail-content .highlight::-webkit-scrollbar {
  width: 0.16rem;
  height: 0.16rem;
}

.detail-content .highlight code {
  overflow: auto;
}

.detail-content .highlight::-webkit-scrollbar-thumb:horizontal {
  border-radius: 0.16rem;
  background-color: rgba(0, 0, 0, 0.5);
}

.detail-content .highlight::-webkit-scrollbar-thumb:horizontal:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.detail-content .highlight pre {
  margin: 0;
  white-space: pre;
}

.detail-content .highlight .hll {
  background-color: #ffc;
}

.detail-content .highlight .err {
  color: #a61717;
  background-color: #e3d2d2;
}

.detail-content .highlight .cp {
  color: #999;
  font-weight: 700;
}

.detail-content .highlight .cs {
  color: #999;
  font-weight: 700;
  font-style: italic;
}

.detail-content .highlight .gd {
  color: #000;
  background-color: #fdd;
}

.detail-content .highlight .gi {
  color: #000;
  background-color: #dfd;
}

.detail-content .highlight .gu {
  color: #aaa;
}

.detail-content .highlight .ni {
