}

.detail-content .highlight .nt {
  color: navy;
}

.detail-content .highlight .w {
  color: #bbb;
}

.detail-content .highlight .sr {
  color: olive;
}

.detail-content [hidden],
.detail-content .button span {
  display: none;
}

.detail-content b,
.detail-content strong,
.detail-content .highlight .k,
.detail-content .highlight .o,
.detail-content .highlight .gs,
.detail-content .highlight .kc,
.detail-content .highlight .kd,
.detail-content .highlight .kn,
.detail-content .highlight .kp,
