}

.detail-content .meta span,
.detail-content .meta .source {
  vertical-align: middle;
}

.detail-content .meta .avatar,
.detail-content .comment-meta .avatar {
  width: 0.533rem;
  height: 0.533rem;
  border-radius: 0.053rem;
  margin-right: 0.133rem;
}

.detail-content .meta .bio,
.detail-content .highlight .gh,
.detail-content .highlight .bp {
  color: #999;
}

.detail-content .night .comment-meta .author,
.detail-content .night .content,
.detail-content .night .meta .author,
.detail-content .highlight .go {
  color: #888;
}

.detail-content .night .headline-title,
.detail-content .night .headline-background .heading-content,
.detail-content .night .question-title {
  color: #B8B8B8;
}

.detail-content .highlight .c,
.detail-content .highlight .cm,
.detail-content .highlight .c1 {
