}

.detail-content .highlight .s,
.detail-content .highlight .sb,
.detail-content .highlight .sc,
.detail-content .highlight .sd,
.detail-content .highlight .s2,
.detail-content .highlight .se,
.detail-content .highlight .sh,
.detail-content .highlight .si,
.detail-content .highlight .sx,
