}

@media only screen and (-webkit-min-device-pixel-ratio2), only screen and (min-device-pixel-ratio2) {
  .detail-content .icon-arrow-right {
    background-image: url(http://static.daily.zhihu.com/img/share-icons@2x.png);
    -webkit-background-size: 2.187rem 1.467rem;
    background-size: 2.187rem 1.467rem;
  }

  .detail-content .icon-vote,
  .detail-content .icon-voted {
    background-image: url(http://static.daily.zhihu.com/img/app/Comment_Vote@2x.png) !important;
    -webkit-background-size: 0.293rem 0.32rem;
    background-size: 0.293rem 0.32rem;
  }

  .detail-content .icon-voted {
    background-image: url(http://static.daily.zhihu.com/img/app/Comment_Voted@2x.png) !important;
  }

  .detail-content .night .icon-vote {
    background-image: url(http://static.daily.zhihu.com/img/app/Dark_Comment_Vote@2x.png) !important;
  }

  .detail-content .from-column {
    background-image: url(http://static.daily.zhihu.com/img/News_Column_Entrance@2x.png) !important;
    -webkit-background-size: 7.467rem 0.8rem;
    background-size: 7.467rem 0.8rem;
  }

  .detail-content .from-column:active {
    background-image: url(http://static.daily.zhihu.com/img/News_Column_Entrance_Highlight@2x.png) !important;
  }

  .detail-content .night .from-column {
    color: #2b82ac;
    background-image: url(http://static.daily.zhihu.com/img/Dark_News_Column_Entrance@2x.png) !important;
  }

  .detail-content .night .from-column:active {
    background-image: url(http://static.daily.zhihu.com/img/Dark_News_Column_Entrance_Highlight@2x.png) !important;
  }
}

.detail-content .meta .meta-item {
  width: 39%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  color: #929292;
  margin-right: 0.187rem;
}

.detail-content .headline .meta {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.293rem;
  color: #b8b8b8;
  margin: 0.533rem 0;
  padding: 0 0.533rem;
}

.detail-content .headline .meta a,
.detail-content .headline .meta a:hover {
  margin-top: 0.053rem;
  float: right;
  font-size: 0.293rem;
  color: #0066cf;
  text-decoration: none;
}

.detail-content .answer h1,
.detail-content .answer h2,
.detail-content .answer h3,
.detail-content .answer h4,
.detail-content .answer h5 {
  font-size: 0.507rem;
}

.detail-content .origin-source,
.detail-content a.origin-source:link {
  display: block;
  margin: 0.667rem 0;
  height: 1.333rem;
  overflow: hidden;
  background: #f0f0f0;
  color: #888;
  position: relative;
  -webkit-touch-callout: none;
}

.detail-content .origin-source .source-logo,
.detail-content a.origin-source:link .source-logo {
  float: left;
  width: 1.333rem;
  height: 1.333rem;
  margin-right: 0.267rem;
}

.detail-content .origin-source .text,
.detail-content a.origin-source:link .text {
  line-height: 1.333rem;
  height: 1.333rem;
  font-size: 0.347rem;
}

.detail-content .origin-source.with-link .text {
  color: #333;
}

.detail-content .origin-source.with-link:after {
  display: block;
  position: absolute;
  border-color: transparent transparent transparent #f0f0f0;
  border-width: 0.187rem;
  border-style: solid;
  height: 0;
  width: 0;
  top: 0.48rem;
  right: 0.107rem;
  line-height: 0;
  content: "";
}

.detail-content .origin-source.with-link:before {
  display: block;
  height: 0;
  width: 0;
  position: absolute;
  top: 0.48rem;
  right: 0.08rem;
  border-color: transparent transparent transparent #000;
  border-width: 0.187rem;
  border-style: solid;
  line-height: 0;
  content: "";
}

.detail-content .origin-source-wrap {
  position: relative;
  background: #f0f0f0;
}

.detail-content .origin-source-wrap .focus-link {
  position: absolute;
  right: 0;
  top: 0;
  width: 1.2rem;
  color: #00a2ed;
  height: 1.333rem;
  display: none;
  text-align: center;
  font-size: 0.32rem;
  -webkit-touch-callout: none;
}

.detail-content .origin-source-wrap .focus-link .btn-label {
  text-align: center;
  display: block;
  margin-top: 0.213rem;
  border-left: solid 0.027rem #ccc;
  height: 0.907rem;
  line-height: 0.907rem;
}

.detail-content .origin-source-wrap.unfocused .focus-link {
  display: block;
}

.detail-content .origin-source-wrap.unfocused .origin-source:before,
.detail-content .origin-source-wrap.unfocused .origin-source:after {
  display: none;
}

.detail-content .night .origin-source-wrap {
  background: #292929;
}

.detail-content .night .origin-source-wrap .focus-link {
  color: #116f9e;
}

.detail-content .night .origin-source-wrap .btn-label {
  border-left: solid 0.027rem #3f3f3f;
}

.detail-content .night .origin-source,
.detail-content .night .origin-source.with-link {
  background: #292929;
  color: #666;
}

.detail-content .night .origin-source .text,
.detail-content .night .origin-source.with-link .text {
  color: #666;
}

.detail-content .night .origin-source.with-link:after {
